import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AccountService, AlertService } from '@app/_services';
import { first } from 'rxjs/operators';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-categoryadd',
  templateUrl: './categoryadd.component.html',
  styleUrls: ['./categoryadd.component.less']
})
export class CategoryaddComponent implements OnInit {

  form: FormGroup;
  id: string;
  loading = false;
  submitted = false;
  categoryId: string;

  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private accountService: AccountService,
    private alertService: AlertService
  ) { }
  add = new FormGroup({
    name: new FormControl('', [Validators.required]),
    status: new FormControl('', [Validators.required]),
  })
  ngOnInit() {
    this.accountService.BlogCategoryById(this.route.snapshot.params.id).subscribe((result: any) => {
      this.add = new FormGroup({
        name: new FormControl(result['name']),
        status: new FormControl(result['status']),
        categoryId: new FormControl(result['categoryId']),

      })
    })

  }


  // convenience getter for easy access to form fields
  get f() { return this.add.controls; }

  onSubmit() {
    this.submitted = true;

    // reset alerts on submit
    this.alertService.clear();

    // stop here if form is invalid
    if (this.add.invalid) {
      return;
    }
    if (this.categoryId = "") {
      console.warn(this.add.value)
      this.loading = true;
      this.accountService.AddBlogCategory(this.add.value)
        .pipe(first())
        .subscribe({
          next: () => {
            this.alertService.success('BlogCategory added successfully', { keepAfterRouteChange: true });
            this.router.navigate(['/admin/categorylist'], { relativeTo: this.route });
          },
          error: error => {
            this.alertService.error(error);
            this.loading = false;
          }
        });
    }
    else {
      this.loading = true;
      this.accountService.UpdateBlogCategory(this.id, this.add.value)
        .pipe(first())
        .subscribe({
          next: () => {
            this.alertService.success('Update successful', { keepAfterRouteChange: true });
            this.router.navigate(['/admin/categorylist'], { relativeTo: this.route });
          },
          error: error => {
            this.alertService.error(error);
            this.loading = false;
          }
        });
    }
    Swal.fire(
      'Done!',
      'Your record has been successfully added',
      'success'
    )
  }
}
