import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AccountService } from '@app/_services';
import { first } from 'rxjs/operators';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-bloglist',
  templateUrl: './bloglist.component.html',
  styleUrls: ['./bloglist.component.less']
})
export class BloglistComponent implements OnInit {


  users: any = null;
  name: any;
  value: any;
  selectvalue: any  ="Choose Category";
  PageNumber = 1;
  count = 0;
  PageSize = 2;
  pageSizes = [5, 10, 25];
  pagecount:any ="Selecte Page per Recode";
  pageIndex=1;
  blogData: any = [];
  blogFilterdata: Array<any> = [];
  blogTempdata: Array<any> = [];
  x=1
 
  Search: string;
columnname: string;
currentTutorial = null;
currentIndex = -1;
p: number = 1;

  constructor(private accountService: AccountService, private route: Router ) { }
  PageSizeCount(){
    this.PageSize=this.pagecount
  }
  getRequestParams(PageNumber, PageSize): any {
    // tslint:disable-next-line:prefer-const
    let params = {};

    if (PageNumber) {
      params[`PageNumber`] = PageNumber;
    }

    if (PageSize) {
      params[`PageSize`] = PageSize;
    }

    return params;
  }
  ngOnInit() {
    const params = this.getRequestParams(this.PageNumber, this.PageSize);
    console.warn(params);
    
    this.accountService.BloggetAll(params)
    .pipe(first())
    .subscribe((blogData)=>{
      debugger
      console.warn(blogData);
         this.blogData = blogData;
      this.blogTempdata = this.blogData;
      
    })

    // users => this.users = users,
    // count => this.count = count
  // );

    // this.accountService.BloggetAll(params).subscribe((blogData) => {
    //   console.warn(blogData);
      
    //   this.blogData = blogData;
    //   this.blogTempdata = this.blogData;
    // })
  }
  deleteblog(id: any) {
    Swal.fire({
      title: 'Are you sure want to remove?',
      text: 'You will not be able to recover this file!',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, keep it'
    }).then((result) => {
      if (result.value) {
        debugger;
        // this.users.splice(id-1,1);
        this.accountService.DeleteBlog(id).subscribe((result) => {
          // const params = this.getRequestParams(this.page, this.pageSize);
          const params = this.getRequestParams(this.PageNumber, this.PageSize);
         
            this.ngOnInit();
        })
       
        Swal.fire(
          'Deleted!',
          'Your imaginary file has been deleted.',
          'success'
        )
      } else if (result.dismiss === Swal.DismissReason.cancel) {
        Swal.fire(
          'Cancelled',
          'Your imaginary file is safe :)',
          'error'
        )
      }
    })
  }


  searchValueResto() {
    console.warn(this.selectvalue, this.value);
    if (this.value !== '') {
      this.blogData = this.blogTempdata;
      if (this.selectvalue == 'title') {

        this.blogFilterdata = this.blogData.filter((res: any) => {

          return res.title.toLocaleLowerCase().match(this.value.toLowerCase());
         
        });
        this.p= this.x
      }
      else if (this.selectvalue == 'meta_Title') {

        this.blogFilterdata = this.blogData.filter((res: any) => {

          return res.meta_Title.toLocaleLowerCase().match(this.value.toLowerCase());
         
        });
        this.p= this.x
      }
      else if (this.selectvalue == 'meta_Keyword') {

        this.blogFilterdata = this.blogData.filter((res: any) => {

          return res.meta_Keyword.toLocaleLowerCase().match(this.value.toLowerCase());
         
        });
        this.p= this.x
      }
      else if (this.selectvalue !== '') {
        this.ngOnInit();
      }
      if (this.blogFilterdata != null) {
        this.blogData = this.blogFilterdata;
      }
    }
    else if (this.value === '') {
      this.ngOnInit();
    }
  }



  //pagination
  handlePageChange(event): void {

    this.PageNumber = event;
    this.ngOnInit();
    }
    
    handlePageSizeChange(event): void {
    debugger
    this.PageSize = event.target.value;
    localStorage.setItem('PagesSize',this.PageSize.toString());
    this.PageNumber = 1;
    
    this.ngOnInit();
    }

  // sorting
  reverse: boolean = false;
  key = 'blogId';
  sort(key: string) {
    this.key = key;
    this.reverse = !this.reverse;
  }


}
