import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AccountService, AlertService } from '@app/_services';
import { first } from 'rxjs/operators';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-tagadd',
  templateUrl: './tagadd.component.html',
  styleUrls: ['./tagadd.component.less']
})
export class TagaddComponent implements OnInit {

  form: FormGroup;
  id: string;
  loading = false;
  submitted = false;
  tagId:string;

  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private accountService: AccountService,
    private alertService: AlertService
  ) { }
  add = new FormGroup({
    name: new FormControl('', [Validators.required])

  })
  ngOnInit() {
    this.accountService.BlogTagsById(this.route.snapshot.params.id).subscribe((result: any) => {
      this.add = new FormGroup({
        name: new FormControl(result['name']),

        tagId: new FormControl(result['tagId']),

      })
    })


  }

  // convenience getter for easy access to form fields
  get f() { return this.add.controls; }

  onSubmit() {
    this.submitted = true;

    // reset alerts on submit
    this.alertService.clear();

    // stop here if form is invalid
    if (this.add.invalid) {
      return;
    }
    if (this.tagId="") {
      console.warn(this.add.value)
      this.loading = true;
      this.accountService.AddTagsCategory(this.add.value)
        .pipe(first())
        .subscribe({
          next: () => {
            this.alertService.success('BlogTags added successfully', { keepAfterRouteChange: true });
            this.router.navigate(['/admin/taglist'], { relativeTo: this.route });
          },
          error: error => {
            this.alertService.error(error);
            this.loading = false;
          }
        });
     
    }
    else{
      this.loading = true;
      this.accountService.UpdateTagsCategory(this.id, this.add.value)
      .pipe(first())
      .subscribe({
        next: () => {
          this.alertService.success('Update successful', { keepAfterRouteChange: true });
          this.router.navigate(['/admin/taglist'], { relativeTo: this.route });
        },
        error: error => {
          this.alertService.error(error);
          this.loading = false;
        }
      });
    }
    Swal.fire(
      'Done!',
      'Your record has been successfully added',
      'success'
    )



  }



}
