import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AccountService, AlertService } from '@app/_services';
import { first } from 'rxjs/operators';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-blogpost',
  templateUrl: './blogpost.component.html',
  styleUrls: ['./blogpost.component.less']
})
export class BlogpostComponent implements OnInit {

  form: FormGroup;
  id: string;
  loading = false;
  submitted = false;
  blogId: string;
  categoryData: any;
  blogFile: File = null; // Variable to store file
  imageSrc: string;
  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private accountService: AccountService,
    private alertService: AlertService
  ) { }
  add = new FormGroup({
    title: new FormControl('', [Validators.required]),
    description: new FormControl('', [Validators.required]),
    status: new FormControl('', [Validators.required]),
    meta_Title: new FormControl('', [Validators.required]),
    meta_Keyword: new FormControl('', [Validators.required]),
    meta_Description: new FormControl('', [Validators.required]),
    categoryId: new FormControl('', [Validators.required]),
  })


  ngOnInit() {
    this.accountService.BlogById(this.route.snapshot.params.id).subscribe((result: any) => {
      console.warn(result);

      this.add = new FormGroup({
        
        title: new FormControl(result['title']),
        description: new FormControl(result['description']),
        image: new FormControl(result['image']),
        status: new FormControl(result['status']),
        meta_Title: new FormControl(result['meta_Title']),
        meta_Keyword: new FormControl(result['meta_Keyword']),
        meta_Description: new FormControl(result['meta_Description']),
        categoryId: new FormControl(result['categoryId']),
        blogId: new FormControl(result['blogId']),

      })
    })

    this.accountService.BlogCategorygetAll1().subscribe((categoryData) => {
      console.warn(categoryData);
      this.categoryData = categoryData;
      // this.TagTempdata = this.TagData;
    })

  }

  // convenience getter for easy access to form fields
  get f() { return this.add.controls; }
  // On file Select
  onBlogFileChange(event) {
    this.blogFile = event.target.files[0];
    debugger;
        const reader = new FileReader();

    if (event.target.files && event.target.files.length) {
      const [file] = event.target.files;
      reader.readAsDataURL(file);

      reader.onload = () => {

        this.imageSrc = reader.result as string;

        this.add.patchValue({
          fileSource: reader.result
        });

      };
  }
}
  onSubmit() {
    this.submitted = true;
    console.warn(this.add.value)
    // reset alerts on submit
    this.alertService.clear();

    // stop here if form is invalid
    if (this.add.invalid) {
      return;
    }

    if (this.add.value.blogId == "" || this.add.value.blogId == "0" || this.add.value.blogId == null) {
      debugger;
      console.warn(this.blogId);
      
      console.warn(this.add.value)
      this.loading = true;
   
      //this.add.get('Image').setValue(this.blogFile);
     
      this.accountService.AddBlog(this.add.value,this.blogFile)
        .pipe(first())
        .subscribe({
          next: () => {
            this.alertService.success('Blog added successfully', { keepAfterRouteChange: true });
            this.router.navigate(['/admin/bloglist'], { relativeTo: this.route });

          },
          error: error => {
            this.alertService.error(error);
            this.loading = false;
          }
        });
    }
    else {
      console.warn(this.add.value)
    console.warn(this.blogFile);
    
      this.loading = true;
      this.accountService.UpdateBlog(this.route.snapshot.params.id, this.add.value,this.blogFile)
        .pipe(first())
        .subscribe({
          next: () => {
            this.alertService.success('Blog Update successfully', { keepAfterRouteChange: true });
            this.router.navigate(['/admin/bloglist'], { relativeTo: this.route });
          },
          error: error => {
            this.alertService.error(error);
            this.loading = false;
          }
        });

    }
    Swal.fire(
      'Done!',
      'Your record has been successfully added',
      'success'
    )

  }

  // onBlogFileChange(event) {
  //   const reader = new FileReader();

  //   if (event.target.files && event.target.files.length) {
  //     const [file] = event.target.files;
  //     reader.readAsDataURL(file);

  //     reader.onload = () => {

  //       this.imageSrc = reader.result as string;

  //       this.add.patchValue({
  //         fileSource: reader.result
  //       });

  //     };

  //   }
  // }


}
